body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.Game {
    text-align: center;
}

.Game .status {
    margin-bottom: 10px;
    padding: 10px;
    background-color: #ccc;
}

.Game .restart {
    text-align: center;
    background: white;
    font-size: 26px;
    line-height: 32px;
}

.Square {
    width: 40px;
    height: 40px;
    background: #eee;
    font-size: 40px;
    line-height: 40px;
    border: 1px outset #ddd;
    cursor: default;
}
.Square.mine {
    background-color: darkred;
    color: white;
}
.Square.revealed {
    background: #ddd;
}
.Square.number1 {
    color: blue;
}
.Square.number2 {
    color: green;
}
.Square.number3 {
    color: red;
}
.Square.number4 {
    color: darkblue;
}
.Square.number5 {
    color: darkred;
}
.Square.number6 {
    color: teal;
}
.Square.number7 {
    color: #333;
}
.Square.number8 {
    color: deeppink;
}
.Square:focus {
    outline: none;
}

